const postToDo=async(datas)=>{
    const url="http://localhost:3000/api/v1/all";
    var raw = JSON.stringify(datas);
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
        var requestOptions = {
        method: 'POST',
        headers: myHeaders,
        body: raw,
        redirect: 'follow'
      };
    const response = await fetch(url,requestOptions)
    const data = await response.json();
    return ({status:true,data:data})
}
const fetchToDos= async()=>{  const url="http://localhost:3000/api/v1/all";
const response = await fetch(url)
const data = await response.json();
return data
}
const checkAuth=async ()=>{
    const url="http://localhost:3000/checkAuth"
    const response = await fetch(url);
    const data= await response.json();
    return data.status;
}

export {postToDo,checkAuth,fetchToDos}