import React,{useState,useEffect} from "react";
import { postToDo,fetchToDos } from "../api";

function TodoCard(props) {
  const [newToDoTitle,setNewTodoTitle]=useState("");
  const [newToDoDetail,setNewTodoDetail]=useState("");
  const [dataState,setDataState]=useState([])

  const sortByTime=(arr)=> arr.sort((a, b) => b.createdAt - a.createdAt)
  useEffect(async()=>{
    const fetchedTodos=await fetchToDos()
    console.log(fetchedTodos)
    setDataState(sortByTime([...fetchedTodos.data]))
  },[])

  const handleSubmit=(e)=>{ 
    e.preventDefault();
    const Tododata={title:newToDoTitle,toDoDetail:newToDoDetail,createdAt:Date.now()}
    setDataState(sortByTime([...dataState,Tododata]))
    postToDo(Tododata)
    setNewTodoTitle("");
    setNewTodoDetail("") }
  return (
   <> 
 <form onSubmit={handleSubmit}>
   <div class="form-group">
       <label for="toDoTitle">Title</label>
       <input type="text" name="title" class="form-control" id="toDoTitle" value={newToDoTitle} placeholder="Enter Title"  onChange={(e) => {
              setNewTodoTitle(e.target.value);
            }}/>
   </div>
   <div class="form-group">
       <label for="exampleInputPassword1">Detail</label>
       <input type="text" name="toDoDetail" class="form-control" id="toDoDetail" placeholder="Enter To-Do in Detail" value={newToDoDetail} onChange={(e) => {
              setNewTodoDetail(e.target.value);
            }} />
   </div>
   <div>
   <button type="submit" id="addNew" class="btn btn-danger btn-sm  d-flex justify-content-end">Add To-Do</button>
   </div>
</form>
   <div className="row">
   {dataState ? dataState.map((todo)=>(
            <div class="card col-3 m-4">
                <div class="card-body">
                    <h5 class="card-title">
                      Title: {todo.title}
                    </h5>
                    <h6 class="card-subtitle mb-2 text-muted">Created At: {new Date(todo.createdAt).toDateString()}</h6>
                    <p class="card-text">
                      Details: {todo.toDoDetail}
                    </p>
                    <div class=" d-flex justify-content-between">
                        <a href="" class="link ">View</a>
                        <a href="" class="link">Remove</a>
                    </div>         
    </div>
</div>
   )):<h1>No Items Found</h1>}
</div>
</>
  )
}

export default TodoCard;
